.timePageWrapper {
    width: 100%;
    height: 100%;
    background: url('https://d2gg9evh47fn9z.cloudfront.net/800px_COLOURBOX41510543.jpg');
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 15px;
}

.timeBox {
    border: 2px solid #ffffff;
    background: rgba(235, 235, 241, 0.531);
    border-radius: 10px;
    padding: 10px;
    .timeBox__time {
        color: rgb(83, 31, 31);
        font-weight: 600;
        font-size: 36px;
    }
}
